.first_section {
    display: flex;
    width: 1400px;
    margin-top: 20px;
    margin-left: 250px;
    flex-direction: row;
}

.card {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(167, 167, 167);
    width: 100%;
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 30px;
    transition: 0.3s;
    cursor: pointer;
  }
  .card:hover {
    background-color: #d5dae0;
  }

  a {
    text-decoration: none;
  }
  
  .card_content {
    display: flex;
    margin: 7px;
  }
  
  .card_image {
    width: 70%;
    height: auto;
  }

  .card_image img {
    width: 100%;
  }
  
  .card-description {
    margin-left: 15px;
  }

  
  .card-description h4 {
    font-family: 'Roboto Mono', monospace;
    font-size: 1rem;
    color: #7e8082;
    font-weight: lighter;
    margin-bottom: 15px;
  }
  
  .card-description p {
    font-weight: lighter;
    color: black;
    font-size: 1rem;
    margin-bottom: 20px;
  }
  
  .card-description a {
    display: inline-block;
    color: #007bff;
    margin-top: 10px;
    margin-left: 15px;
    text-decoration: none;
  }
  
.middle_section {
    width: 1400px;
    margin-top: 35px;
    margin-left: 250px;
    flex-direction: row;
}


.technology {
  display: flex;
}

.heading {
  width: 120%;
}

.heading p {
  font-family: 'Inconsolata', monospace;
  font-size: 45px;
}

.technology_section p {
  margin-bottom: 25px;
  font-size: 23px;
  color: rgb(35, 34, 34);
}
a {
  color: rgb(49, 49, 249);
}

.tags a {
  display: block;
  margin-top: 10px;
}

.last_section .categories {
  width: 1400px;
  margin-top: 70px;
  margin-left: 250px;
  display: flex;
  flex-wrap: wrap;
}

.category {
  width: calc(25%);
  height: 200px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: solid 1px #ccc;
  transition: 0.3s;
  cursor: pointer;
}

.category:hover {
  background-color: #e4e4e4;
}

.categories a {
  text-decoration: none;
  color: black;
}

.category_bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.icon {
   font-size: 55px; 
}

.paragraph1, .paragraph2 {
   font-size: medium; 
   margin-bottom:5px; 
   transition: transform 2s;
}

.arrow {
    font-size: 25px; 
}

.category .paragraph2 {
  display: none;
  transform: translateY(-100%);
}

.category:hover .paragraph2 {
  display: block;
  transform: translateY(0%);
}

.category:hover .paragraph1 {
  display: none;
  transform: translateY(100%);
}